<template>
  <div class="stores-wrapper">
    <app-bg
        :height="o.height" :src="`stores/${o.name}.jpg`" v-for="(o, i) in bglist" :key="i"/>
  </div>
</template>

<script>
import AppBg from '@/components/AppBg'
export default {
  name: 'stores',
  data() {
    return {
        bglist: [
          {
            height: 312,
            name: '3_02'
          },
          {
            height: 399,
            name: '3_03'
          },
          {
            height: 382,
            name: '3_04'
          },
          {
            height: 312,
            name: '3_05'
          },
          {
            height: 273,
            name: '3_06'
          }
        ]
    }
  },
  created() {
  },
  mounted() {
  },
  components: {
    AppBg
  }
}
</script>

<style scoped lang="less">
.stores-wrapper{
  margin-top: -12px;
}
</style>



